
import { defineComponent } from "vue";
import { modalController } from "@ionic/vue";
import { TRN24500112_REQ_BODY, TRN24500112_RES, FUND_TRANSFER, ATTACH_INFO_LIST } from "@/shared/type/TRN24500112.type";
import { TRN24500132_BODY, TRN24500132_RES } from "@/shared/type/TRN24500132.type";
import VueDatePicker from "@vuepic/vue-datepicker";
import BreadCrumb from "@/shared/components/BreadCrumb/BreadCrumb.vue";
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";
import PopupService from '@/shared/services/PopupService';
import trn245001sc03 from "../trn245001sc03/trn245001sc03.vue";
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue';
import { APM20200113_RES } from '@/shared/type/APM20200113.type';
import { screenMode } from '@/shared/type/Approval.type';
import { FILEID, GET_PDF_REQ, GET_PDF_RES } from "@/shared/type/TRN24500111.type";

const popup = new PopupService();

export default defineComponent({
  name: "trn245001sc01",
  components: {
    VueDatePicker,
    BreadCrumb,
    SelectOption,
    ModifyCard
  },
  data() {
    return {
      fundTransferInfo: {} as FUND_TRANSFER,
      fundTransferInform: {
        status: "",
        remark: "",
      },
      modifyCard: {
        registerTellerId: "",
        registerTellerName: "",
        registerDate: "",
        registerTime: "",
        updatedTellerId: "",
        updatedTellerName: "",
        updatedDate: "",
        updatedTime: "",
        deleteTellerId: "",
        deleteTellerName: "",
        deleteDate: "",
        deleteTime: "",
      },
      isEditable: false,
      isInValidInput: false,
      approvalRequestDate: "",
      isChange: false,
      isDisabledSelect: false,
      isDisabledSelectProcessing: false,
      approvalRes: {} as any,
      operationMode: 'normal' as screenMode,
      applicationStatusCode: '',
    };
  },

  computed: {
    queryRoute() {
      return this.$route.query;
    },
    workProgressStatusCode() {
      return this.$store.state.CommonCodeModule.commonCode.WorkProgressStatusCode;
    },
    approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
    },
  },

  async created() {
    if (this.approvalType === '') {
      await this.fundTransferDetail();
    } else {
      await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
        let authDetailParsed = JSON.parse(authDetail.body.requestContent)

        const _oBody: TRN24500112_REQ_BODY = {
          approvalRequestDate: String(authDetailParsed.approvalRequestDate),
          internetBankingApprovalNo: Number(authDetailParsed.internetBankingApprovalNo),
        };

        const res = (await this.$request.getRequest('TRN24500112', _oBody, false)) as TRN24500112_RES;

        this.approvalRes = authDetail.body
        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'

        this.fundTransferInfo = res.body

        if (res.header.result) {
          this.fundTransferInfo.beneficiaryRelationshipStatusCode = `${this.$codeConverter.convertValue('BeneficiaryRelationshipStatusCode', this.fundTransferInfo.beneficiaryRelationshipStatusCode)}`
          this.fundTransferInfo.beneficiaryPurpose = `${res.body.beneficiaryPurpose ? res.body.beneficiaryPurpose + ' : ' : ''} ${this.$codeConverter.convertValue("OverseasPaymentPurposeCode", res.body.beneficiaryPurpose)}`,
          this.fundTransferInfo.overseasPaymentCode = `${res.body.overseasPaymentCode ? res.body.overseasPaymentCode + ' : ' : ''} ${this.$codeConverter.convertValue("OverseasPaymentCode", res.body.overseasPaymentCode)}`,
          this.fundTransferInfo.transactionDate = this.$datetime.formatStringToStringDate(this.fundTransferInfo.transactionDate)
          this.fundTransferInfo.transferAmount = this.$format.formatCurrency(Number(this.fundTransferInfo.transferAmount), this.fundTransferInfo.transferCurrencyCode);
          this.fundTransferInfo.ourFee = this.$format.formatCurrency(Number(this.fundTransferInfo.ourFee), this.fundTransferInfo.equivalentCurrencyCode);
          this.fundTransferInfo.swiftFee = this.$format.formatCurrency(Number(this.fundTransferInfo.swiftFee), this.fundTransferInfo.senderAccountCurrencyCode);
          this.fundTransferInfo.totalDebitAmount = this.$format.formatCurrency(Number(this.fundTransferInfo.totalDebitAmount), this.fundTransferInfo.senderAccountCurrencyCode);
          this.fundTransferInfo.feeAmount = this.$format.formatCurrency(Number(this.fundTransferInfo.feeAmount), this.fundTransferInfo.senderAccountCurrencyCode);
          this.fundTransferInfo.senderAccountNo = this.$format.formatAccountNumber(this.fundTransferInfo.senderAccountNo);
          this.fundTransferInfo.beneficiaryAccountNumber = this.$format.formatAccountNumber(this.fundTransferInfo.beneficiaryAccountNumber);
          this.fundTransferInfo.beneficiaryCountryCode = `${res.body.beneficiaryCountryCode ? res.body.beneficiaryCountryCode + ' : ' : ''} ${this.$codeConverter.convertValue("BeneficiaryCountryCode", res.body.beneficiaryCountryCode)}`;
          const dateObj = this.$datetime.convertStringToDate(this.fundTransferInfo.approvalRequestDate);
          this.approvalRequestDate = this.$datetime.formatVueDatePicker(dateObj);
          this.fundTransferInform.status = this.fundTransferInfo.workProgressStatusCode;
          this.fundTransferInform.remark = this.fundTransferInfo.remark;

          this.isDisabledSelect = res.body.workProgressStatusCode === '01' ? true : false
          this.isDisabledSelectProcessing = res.body.workProgressStatusCode === '02' || res.body.workProgressStatusCode === '03' ? true : false
        }
      })
    }
  },

  methods: {
    async downloadPDF(item: ATTACH_INFO_LIST) {
      await this.$file.downloadImage(item.fileId)
    },

    async fundTransferDetail() {
      this.isEditable = true;

      const _oBody: TRN24500112_REQ_BODY = {
        approvalRequestDate: String(this.queryRoute.approvalRequestDate),
        internetBankingApprovalNo: Number(this.queryRoute.internetBankingApprovalNo),
      };

      const res = (await this.$request.getRequest("TRN24500112", _oBody, false)) as TRN24500112_RES;

      this.fundTransferInfo = JSON.parse(JSON.stringify(res.body));

      if (res.header.result) {
        this.modifyCard = {
          registerTellerId: '',
          registerTellerName: '',
          registerDate: '',
          registerTime: '',
          updatedTellerId: res.body.lastChangeTellerID,
          updatedTellerName: res.body.lastChangeTellerName,
          updatedDate: res.body.lastChangeDate,
          updatedTime: res.body.lastChangeTime,
          deleteTellerId: '',
          deleteTellerName: '',
          deleteDate: '',
          deleteTime: ''
        }

        this.fundTransferInfo.beneficiaryRelationshipStatusCode = `${this.$codeConverter.convertValue('BeneficiaryRelationshipStatusCode', this.fundTransferInfo.beneficiaryRelationshipStatusCode)}`
        this.fundTransferInfo.beneficiaryPurpose = `${res.body.beneficiaryPurpose ? res.body.beneficiaryPurpose + ' : ' : ''} ${this.$codeConverter.convertValue("OverseasPaymentPurposeCode", res.body.beneficiaryPurpose)}`,
        this.fundTransferInfo.overseasPaymentCode = `${res.body.overseasPaymentCode ? res.body.overseasPaymentCode + ' : ' : ''} ${this.$codeConverter.convertValue("OverseasPaymentCode", res.body.overseasPaymentCode)}`,
        this.fundTransferInfo.transactionDate = this.$datetime.formatStringToStringDate(this.fundTransferInfo.transactionDate)
        this.fundTransferInfo.transferAmount = this.$format.formatCurrency(Number(this.fundTransferInfo.transferAmount), this.fundTransferInfo.transferCurrencyCode);
        this.fundTransferInfo.ourFee = this.$format.formatCurrency(Number(this.fundTransferInfo.ourFee), this.fundTransferInfo.transferCurrencyCode);
        this.fundTransferInfo.swiftFee = this.$format.formatCurrency(Number(this.fundTransferInfo.swiftFee), this.fundTransferInfo.senderAccountCurrencyCode);
        this.fundTransferInfo.totalDebitAmount = this.$format.formatCurrency(Number(this.fundTransferInfo.totalDebitAmount), this.fundTransferInfo.senderAccountCurrencyCode);
        this.fundTransferInfo.feeAmount = this.$format.formatCurrency(Number(this.fundTransferInfo.feeAmount), this.fundTransferInfo.senderAccountCurrencyCode);
        this.fundTransferInfo.senderAccountNo = this.$format.formatAccountNumber(this.fundTransferInfo.senderAccountNo);
        this.fundTransferInfo.beneficiaryAccountNumber = this.$format.formatAccountNumber(this.fundTransferInfo.beneficiaryAccountNumber);
        this.fundTransferInfo.beneficiaryCountryCode = `${res.body.beneficiaryCountryCode ? res.body.beneficiaryCountryCode + ' : ' : ''} ${this.$codeConverter.convertValue("BeneficiaryCountryCode", res.body.beneficiaryCountryCode)}`;
        const dateObj = this.$datetime.convertStringToDate(this.fundTransferInfo.approvalRequestDate);
        this.approvalRequestDate = this.$datetime.formatVueDatePicker(dateObj);
        this.fundTransferInform.status = this.fundTransferInfo.workProgressStatusCode;
        this.fundTransferInform.remark = this.fundTransferInfo.remark;

        this.isDisabledSelect = res.body.workProgressStatusCode === '01' ? true : false
        this.isDisabledSelectProcessing = res.body.workProgressStatusCode === '02' || res.body.workProgressStatusCode === '03' ? true : false
      }
    },

    handleCancel() {
      this.$dialog.onOpen({
        content: `${this.$t("message.cancel_without_saving_changes")}`,
        dialogMode: "Question",
        callback: () => {
          this.fundTransferDetail();
          this.isInValidInput = false;
          this.isEditable = true;
        },
      });
    },

    handleSave() {
      this.isInValidInput = false;

      if (this.fundTransferInform.remark === "") {
        return this.isInValidInput = true;
      }

      this.$dialog.onOpen({
        content: this.$t("message.would_you_like_to_save"),
        dialogMode: "Question",
        callback: () => {
          if (this.queryRoute.type === "edit") {
            this.updateFundTransferRequest().then((res: boolean) => {
              if (res) {
                this.isEditable = true;
                this.fundTransferDetail()
                modalController.dismiss();
              }
            });
          }
          modalController.dismiss();
        },
      });
    },

    async updateFundTransferRequest() {
      const _oBody: TRN24500132_BODY = {
        approvalRequestDate: this.fundTransferInfo.approvalRequestDate,
        remark: this.fundTransferInform.remark,
        internetBankingApprovalNo: this.fundTransferInfo.internetBankingApprovalNo,
        workProgressStatusCode: this.fundTransferInform.status,
      };

      const res = (await this.$request.getRequest("TRN24500131", _oBody, false)) as TRN24500132_RES;

      if (res.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode: "Success",
          callback: () => {
            this.fundTransferDetail()
            modalController.dismiss();
          },
        });
      }

      return res.header.result;
    },

    async onGetPdfApi() {
      const requestBody = {
        approvalRequestDate: String(this.queryRoute.approvalRequestDate),
        internetBankingApprovalNo: Number(this.queryRoute.internetBankingApprovalNo),
      } as GET_PDF_REQ

      const responseData = await this.$request.getRequest('TRN24500113', requestBody, false) as GET_PDF_RES

      if (responseData.header) {
        const pdfFile = responseData.body.fileId;
        this.onClickCallInvoicePreviewContent(pdfFile)
      }
    },

    onClickCallInvoicePreviewContent(fileID: FILEID) {
      fileID
      popup.onOpen({
        dataProp: {
          PDF_FIlE_ID: fileID
        },
        component: trn245001sc03,
        callback: (v) => {
          v
        }
      })
    },

    handleClickAction(actionType: string) {
      switch (actionType) {
        case "edit":
          this.isEditable = false;
          break;
        default:
          this.goToList();
          break;
      }
    },

    goToList() {
      this.$router.push("TRN245001SC00");
    },

    formatMoney(numbers: number, currency: string): string {
      let format = "";
      const dotNum = 2;

      if (currency === "USD") {
        format = String(
          Number(numbers).toFixed(dotNum).replace(/\B(?=(\d{3})+(?!\d))/g, ",")
        );
      } else {
        format = String(Math.round(numbers)).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      }
      return format;
    },
  },
});
