
import { PropType, defineComponent } from 'vue'
import PDFPopup from '@/shared/components/PDFPopup/PDFPopup.vue'
export default defineComponent({
    name: 'vba202002sp01',
    props: {
        PDF_FIlE_ID: {
            type: [] as PropType<string>,
            default: [],
            required: true
        }
    },
    components: {
        PDFPopup
    },
    data() {
        return {
            servicePayload: '' as string,
            pdfData: '' as string,
            pdfConfig: {
                modalTitle: 'Preview International Fund Transfer Request',
                fileName: 'International Fund Transfer Request',
                paginate: true
            },
        }
    },
    mounted() {
        this.servicePayload = this.PDF_FIlE_ID;
        this.renderInvoicePDF();
    },
    methods: {
        async renderInvoicePDF() {
            this.pdfData = await this.$file.previewImage(this.servicePayload);
        },
    }
})
